import { Component, OnInit } from '@angular/core';
import { RequestService } from 'src/app/api/request.service';
import { Model } from 'src/app/api/models/model';
import { ActivatedRoute } from '@angular/router';
import { DialogService } from 'src/app/api/util/dialog.service';
import { UtilArrayService } from 'src/app/services/util-array.service';

@Component({
  selector: 'app-team-games',
  templateUrl: './team-games.page.html',
  styleUrls: ['./team-games.page.scss'],
})
export class TeamGamesPage implements OnInit {

  public id : any;
  public status : any;
  public modelTeam : Model;
  public team : any;

  constructor(
    public route : ActivatedRoute,
    public requestService : RequestService,
    public dialogService : DialogService,
    public utilArray : UtilArrayService
  ) {
    this.id = route.snapshot.paramMap.get('id');
    this.status = route.snapshot.paramMap.get('status');
    this.modelTeam = new Model('Team',requestService);
   }

  ngOnInit() {
    this.init();
  }

  async init(event = null){

    await this.dialogService.loadingProgres();

    let parms = {};
    if(this.status)
      parms['status'] = this.status;

    this.modelTeam.api_functionModel(this.id,'pageGames',parms).subscribe(
      response => {
        if(response['status'] == 'success'){
          this.team = response['data'];

        }
      },
      error => {
        this.dialogService.dimissLoaging();
        if(event)
          event.target.complete();
      },
      ()=>{
        this.dialogService.dimissLoaging();
        if(event)
          event.target.complete();
      }
    );
    
  }

  deleteGame(game){
    this.utilArray.listDelete(this.team.games,game.id);
  }

}
